import React from "react";
import { Container } from "./Line.styled";

export interface StateProps {
  type: number;
}

export interface DispatchProps {}

type Props = StateProps & DispatchProps;

export const Line: React.FC<Props> = ({ type }) => {
  return <Container>{lines(type)}</Container>;
};

const lines = (type: number) => {
  switch (type) {
    case 0:
      return <div className=""></div>;
    case 1:
      return (
        <>
          <div className="horizontal" />
        </>
      );
    case 2:
      return (
        <>
          <div className="vertical" />
        </>
      );
    case 3:
      return (
        <>
          <div className="h" />
          <div className="v" />
        </>
      );
    case 4:
      return (
        <>
          <div className="left" />
        </>
      );
    case 5:
      return (
        <>
          <div className="right" />
        </>
      );
    default:
      return null;
  }
};
