import React, { useState } from "react";
import { Container } from "./Skill.styled";
import { SkillData, SkillState } from "../type";

export interface StateProps {
  data: SkillData;
  skill: SkillState;
}

export interface DispatchProps {
  onClick: (skill: string) => void;
}

type Props = StateProps & DispatchProps;

export const Skill: React.FC<Props> = ({ data, skill, onClick }) => {
  return (
    <Container
      onClick={() => {
        if (!skill) onClick(data.name);
        else onClick(skill.name);
      }}>
      <div>{data.name}</div>
      <div className="level">{skill?.level == data.max ? "max" : skill?.level}</div>
    </Container>
  );
};
