import React, { useState } from "react";
import { Container } from "./SkillBranch.styled";
import { SkillData, SkillState } from "../type";
import { Skill } from "../skill/Skill";
import { Line } from "../lines/Line";

export interface LocalProps {
  name: string;
  skills: SkillData[];
}

export interface StateProps {
  points: number;
  totalPoints: number;
  skillsState: { [id: string]: SkillState };
}

export interface DispatchProps {
  onSelectSkill: (skill: string) => void;
}

type Props = StateProps & DispatchProps;

export const SkillBranch: React.FC<Props & LocalProps> = ({
  name,
  skills,
  points,
  skillsState,
  totalPoints,
  onSelectSkill
}) => {
  if (skills.length < 1) return <div>Empty!</div>;

  function sumLevel() {
    let sum = 0;
    for (let i = 0; i < 10; i++) {
      if (skillsState[skills[i].name]) sum += skillsState[skills[i].name].level;
    }

    return sum;
  }

  return (
    <Container>
      <div id="header" className="header">
        <div className="name">{name}</div>
        <div className="level">Lv.{sumLevel()}</div>
        <div>{`${points}/${totalPoints}`}</div>
      </div>
      <div className="tree">
        <div id="body" className="branches">
          <div className="row">
            <Line type={0} />
            <Line type={0} />
            <Skill data={skills[0]} skill={skillsState[skills[0].name]} onClick={onSelectSkill} />
            <Line type={0} />
            <Line type={0} />
          </div>
          <div className="row">
            <Line type={5} />
            <Line type={1} />
            <Line type={3} />
            <Line type={1} />
            <Line type={4} />
          </div>
          <div className="row">
            <Skill data={skills[1]} skill={skillsState[skills[1].name]} onClick={onSelectSkill} />
            <Line type={0} />
            <Skill data={skills[4]} skill={skillsState[skills[4].name]} onClick={onSelectSkill} />
            <Line type={0} />
            <Skill data={skills[7]} skill={skillsState[skills[7].name]} onClick={onSelectSkill} />
          </div>
          <div className="row">
            <Line type={2} />
            <Line type={0} />
            <Line type={2} />
            <Line type={0} />
            <Line type={2} />
          </div>
          <div className="row">
            <Skill data={skills[2]} skill={skillsState[skills[2].name]} onClick={onSelectSkill} />
            <Line type={0} />
            <Skill data={skills[5]} skill={skillsState[skills[5].name]} onClick={onSelectSkill} />
            <Line type={0} />
            <Skill data={skills[8]} skill={skillsState[skills[8].name]} onClick={onSelectSkill} />
          </div>
          <div className="row">
            <Line type={2} />
            <Line type={0} />
            <Line type={2} />
            <Line type={0} />
            <Line type={2} />
          </div>
          <div className="row">
            <Skill data={skills[3]} skill={skillsState[skills[3].name]} onClick={onSelectSkill} />
            <Line type={0} />
            <Skill data={skills[6]} skill={skillsState[skills[6].name]} onClick={onSelectSkill} />
            <Line type={0} />
            <Skill data={skills[9]} skill={skillsState[skills[9].name]} onClick={onSelectSkill} />
          </div>
        </div>
      </div>
    </Container>
  );
};
