import styled from "@emotion/styled";

export const Container = styled.div`
  /* position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); */

  .header {
    display: flex;

    .name {
      font-weight: bolder;
      margin-right: 10px;
    }

    *:last-child {
      margin-left: auto;
    }

    margin-bottom: 10px;
  }

  .tree {
    position: relative;
    .branches {
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0);


      .row {
        display: flex;
      }
    }
  }
`;
