import styled from "@emotion/styled";

export const Container = styled.div`
  height: 75px;
  width: 76px;
  position: relative;
  display: flex;

  .horizontal {
    margin-top: 50%;
    height: 50%;
    width: 100%;
    border-top: 1px solid black;
  }
  .vertical {
    height: 100%;
    width: 50%;
    border-right: 1px solid black;
  }
  .h {
    margin-top: 50%;
    height: 50%;
    width: 100%;
    border-top: 1px solid black;
    position: absolute;
  }
  .v {
    height: 100%;
    width: 50%;
    border-right: 1px solid black;
    position: absolute;
  }
  .left {
    margin-top: 50%;
    height: 50%;
    width: 50%;
    border-right: 1px solid black;
    border-top: 1px solid black;
  }

  .right {
    margin-top: 50%;
    margin-left: 50%;
    height: 50%;
    width: 50%;
    border-left: 1px solid black;
    border-top: 1px solid black;
  }
`;
