import { StateProps, DispatchProps, SkillBranch } from "./SkillBranch";
import { Dispatch } from "redux";
import { connect } from "react-redux";
import { GlobalState } from "../../../store";
import { SkillState } from "../type";
import { setSkillState, selectSkill } from "../action";

const mapStateToProps = (state: GlobalState): StateProps => ({
  points: state.skillTree.points,
  totalPoints: state.skillTree.totalPoints,
  skillsState: state.skillTree.skillState
});

const mapDispatchToProps = (dispatch: Dispatch): DispatchProps => ({
  onSelectSkill: skill => dispatch(selectSkill(skill))
});

export default connect(mapStateToProps, mapDispatchToProps)(SkillBranch);
