import React, { useState } from "react";
import { Container } from "./SkillTree.styled";
import { TabView, TabPanel } from "primereact/tabview";
import { SkillTreeData, SkillState } from "./type";
import { SkillBranch } from "./skillbranch";
import { Dialog } from "primereact/dialog";
import { Button } from "primereact/button";

export interface StateProps {
  skillData: SkillTreeData;
  points: number;
  totalPoints: number;
  skillState: { [id: string]: SkillState };
  selectedSkill: string;
}

export interface DispatchProps {
  onSelectSkill: () => void;
  onSkillUpdate: (skill: SkillState) => void;
}

type Props = StateProps & DispatchProps;

export const SkillTree: React.FC<Props> = ({
  skillData,
  selectedSkill,
  points,
  totalPoints,
  skillState,
  onSelectSkill,
  onSkillUpdate
}) => {
  const [activeIndex, setActiveIndex] = useState(0);

  const data = [...skillData.originality, ...skillData.power, ...skillData.solidness, ...skillData.warm].find(
    x => x.name == selectedSkill
  );

  const skill = skillState[selectedSkill]
    ? skillState[selectedSkill]
    : ({ name: data?.name, level: 0, points: 0 } as SkillState);

  function header() {
    return `${selectedSkill}`;
  }

  function body() {
    if (!data || selectedSkill == "") return null;

    return (
      <>
        <div className="header">
          <label>Lv.{skill.level}</label>
        </div>
        <div className="info">
          {data.effects.map(x => (
            <div key={x.info}>{`${x.info}${x.effect[skill.level]}${x.type}`}</div>
          ))}
        </div>
        <div className="desc">
          {data.effects
            .filter(x => x.description != "")
            .map(x => (
              <div key={x.description}>&#9654;{`${x.description}`}</div>
            ))}
        </div>
      </>
    );
  }

  function footer() {
    if (!data || selectedSkill == "") return null;

    return (
      <div>
        <Button
          icon="pi"
          onClick={() => {
            if (skill.level < 1) return;

            const level = skill.level - 1;
            const points = skill.points - calcPoint(level, data.rank);

            onSkillUpdate({ ...skill, level, points });
          }}>
          {`- ${calcPoint(skill.level, data.rank)}`}
        </Button>
        <Button
          icon="pi"
          onClick={() => {
            if (skill.level == data.max) return;

            const level = skill.level + 1;
            const points = skill.points + calcPoint(level, data.rank);

            onSkillUpdate({ ...skill, level, points });
          }}>
          {`+ ${calcPoint(skill.level + 1, data.rank)}`}
        </Button>
      </div>
    );
  }

  function dialog() {
    return (
      <Dialog
        className="dialog"
        header={header()}
        dismissableMask={true}
        closeOnEscape={true}
        visible={selectedSkill != ""}
        style={{ width: "100%", maxWidth: "500px" }}
        footer={footer()}
        onHide={() => onSelectSkill()}>
        {body()}
      </Dialog>
    );
  }

  return (
    <Container>
      <TabView activeIndex={activeIndex} onTabChange={e => setActiveIndex(e.index)}>
        <TabPanel header="Originality">
          <SkillBranch name={"Originality"} skills={skillData.originality} />
        </TabPanel>
        <TabPanel header="Warmth">
          <SkillBranch name={"Warmth"} skills={skillData.warm} />
        </TabPanel>
        <TabPanel header="Solidness">
          <SkillBranch name={"Solidness"} skills={skillData.solidness} />
        </TabPanel>
        <TabPanel header="Power">
          <SkillBranch name={"Power"} skills={skillData.power} />
        </TabPanel>
      </TabView>
      {dialog()}
    </Container>
  );
};

function calcPoint(level: number, rank: number) {
  if (level <= 0) return 0;

  if (rank == 1) {
    return (1 / 4) * (2 * level + Math.pow(-1, level) + 3);
    // 1 2 2 3 3 4 4
  }

  if (rank == 2) {
    return (1 / 4) * (2 * level + Math.pow(-1, level + 1) + 5);
    // 2 2 3 3 4 4
  }

  if (rank == 3) {
    return (1 / 4) * (2 * level + Math.pow(-1, level) + 11);
    // 3 4 4 5 5
  }

  return 0;
}
