import { StateProps, DispatchProps, SkillTree } from "./SkillTree";
import { Dispatch } from "redux";
import { connect } from "react-redux";
import { GlobalState } from "../../store";
import { selectData } from "./selector";
import { selectSkill, setSkillState } from "./action";

const mapStateToProps = (state: GlobalState): StateProps => ({
  skillData: state.skillTree.data,
  points: state.skillTree.points,
  totalPoints: state.skillTree.totalPoints,
  selectedSkill: state.skillTree.selectedSkill,
  skillState: state.skillTree.skillState
});

const mapDispatchToProps = (dispatch: Dispatch): DispatchProps => ({
  onSelectSkill: () => dispatch(selectSkill("")),
  onSkillUpdate: skill => dispatch(setSkillState(skill))
});

export default connect(mapStateToProps, mapDispatchToProps)(SkillTree);
